.home_sale_sec {
  position: relative;
  background-image: url("../images/Home@2x.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  .container {
    padding: 130px 0px 130px 0px;
  }
  .villa_sale_card {
    width: 540px;
    background-color: $light;
    padding: 50px;
    border-radius: 5px;
    .villa_heading {
      font-family: OpenSans-semiBold;
      font-size: $font-size-md;
      font-weight: normal;
    }
    .villa_price {
      font-family: OpenSans-Bold;
      font-size: $font-size-md-2;
      font-weight: bold;
    }
    .villa_facilities {
      max-width: 320px;
      font-family: OpenSans;
      ul {
        @include flexBetween;
        li {
          //   height: 40px;
          @include flexBetween;
          &:last-child svg {
            height: 22px;
          }
        }
        span {
          svg {
            width: 30px;
            height: 30px;
            margin-right: 10px;
            path {
              fill: $muted;
            }
          }
        }
      }
    }
  }
  .villa_detail_card {
    @extend .villa_sale_card;
    padding: 20px 50px;
    @include flexBetween;
    font-family: OpenSans;
    img {
      width: 80px;
      height: auto;
      margin-right: 26px;
      border-radius: 10px;
    }
    ul {
      margin-right: auto;
      li {
        line-height: 1;
        &:not(:last-child) {
          font-size: $font-size-regular;
        }
        &:last-child {
          font-size: $font-size-small;
        }
      }
    }
  }
}
