nav {
  position: fixed;
  top: 0px;
  z-index: $z-index-high;
  background-color: transparent;
  width: 100%;
  margin: auto;
  .burger_btn {
    visibility: hidden;
  }
  .nav_box {
    padding: 20px 0px;
    @include flexBetween;
  }
  .nav_wrapper {
    width: 100%;
    @include flexBetween;
    a img {
      margin-right: 50px;
      width: 160px;
      height: auto;
    }
  }
  .nav_menus {
    @include flexBetween;
    .nav_menu__item{
        cursor: pointer;
        font-family: OpenSans-Bold;
        font-weight: 400;
        font-size: $font-size-small;
        a{
            text-decoration: none;
            color: $light;
        }
   
        &:not(:last-child) {
            margin-right: 40px;
          }
    }
  }
  .nav_icon_sec {
    margin-left: auto;
    cursor: pointer;
    a {
      svg {
        width: 25px;
        height: auto;
      }
      svg path {
        fill: $light;
      }
      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }
}
