.sell_property {
  .sell_wrapper {
    @include flexBetween;
    align-items: flex-start;
    .sell_card {
      @include card;
      flex-direction: column;
      h3 {
        font-family: OpenSans-Bold;
        font-size: $font-size-xl;
        font-weight: bold;
        line-height: 1.2;
        max-width: 380px;
      }
      p {
        font-family: OpenSans;
        font-size: $font-size-regular;
        line-height: 1.4;
        max-width: 330px;
        color: $muted;
      }
      &:last-child {
        p {
          color: $light;
        }
      }
      &:not(:last-child) {
        margin-right: 36px;
      }
    }
    @include breakpoint(map-get($breakpoints, "mobile")) {
        flex-direction: column;
    }
  }
}
